@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient__bg {

    /* ff 3.6+ */

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 10% 35%, rgba(123, 69, 210, 1) 0%, rgba(0, 5, 107, 1) 100%);

    position : relative;

      

}

.gradient__bg:after {
    content  : "";
    position : absolute;
    z-index  : 1;
    bottom   : 0;
    left     : 0;
    pointer-events   : none;
    background-image : linear-gradient(to bottom, 
                      rgba(255,255,255, 0), 
                      var(--color-bg) 90%);
    width    : 100%;
    height   : 4em;
  }

.gradient__text {
    background: var(--gradient-text);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-background-clop: text;
    -webkit-text-fill-color: transparent;
}

.section__padding {
    padding: 4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
@keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }

@media screen and (max-width: 700px) {
    .section__padding {
        padding: 4rem;
    }

    .section__margin {
        margin: 4rem;
    }
}

@media screen and (max-width: 550px) {
    .section__padding {
        padding: 4rem 2rem;
    }

    .section__margin {
        margin: 4rem 2rem;
    }
}



.footer {
    background: linear-gradient(to right, rgb(153, 0, 255), rgb(25, 0, 255));
    height: 10vh;
}

.footer-title {
    color: rgb(0, 195, 255);
    font-size: 1.4rem;
    padding: 1rem 2rem;
}

.footer-title span{
    color: rgb(0, 160, 209);
    font-weight: 800;
}
.xxa__home {
    display: flex;
    /* min-height: 100vh; */
    padding-bottom: 8rem;
}

.xxa__home-content {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.xxa__home-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
    margin-top: 1em;
}

.xxa__home-content p {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin: 1.5rem 0rem;
}

.xxa__home-button {
    flex: 0.6 1;
    min-width: 15rem;
    min-height: 50px;
    background: orangered;
    border-radius: 17px;
    margin: 0.5rem;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    color: #fff;
    padding: 1rem;
    cursor: pointer;
    text-align: center;
    transition: 0.1s ease;
}

.xxa__home-button:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    background: rgb(228, 61, 0);
}

.xxa__home-skills {
    flex: 1 1;
    display: felx;
    justify-content: center;
    align-items: center;
}

.xxa__home-skills img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .xxa__home {
        flex-direction: column;
    }

    .xxa__home-content {
        margin: 3rem;
    }
}

@media screen and (max-width: 650px) {
    .xxa__home h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .xxa__home p {
        font-size: 16px;
        line-height: 24px;
    }
    
    .xxa__home-button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .xxa__home h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .xxa__home p {
        font-size: 14px;
        line-height: 24px;
    }
    
    .xxa__home-button {
        font-size: 12px;
        line-height: 16px;
    }
}

