* {
    font-family: var(--font-family);
}

.xxa__proj {
    padding: 10rem;
}

@media screen and (max-width: 600px) {
    .xxa__proj {
        padding: 2rem 1rem;
    }
}

.list{
    background-color: rgba(0, 0, 0, 0.6);
    max-width: 90vw;
    border-radius: 10px;
}

.head{
    padding-bottom: 0.7em;
    box-shadow: 0px 5px 10px rgba(1 1 1/ 15%);
    font-size: 2em;
    padding-left: 1em;
    padding-top: 0.3em;
    color: #fff;
    text-align: center;
    color: var(--color-text);
}


.item-about {
    display: flex;
    overflow: hidden;
    max-height: 0vh;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    font-size: 0.6em;
    transition: 1s ease;
}

.item{
    padding: 0.5em 0.5em;
    color: #fff;
    font-size: 2em;
}

.item:hover .item-about {
    max-height: 100vh;
}


.info{
    text-align: center;
}

.info h3{
    color: #4487fc;
    font-size: 1.2em;
    font-weight: 700;
    margin: 10px;

}

.icon{
    color: #4487fc;
    font-size: 3em;
    text-align: center;
}

.card{
    background-color: #eeeeee0e;
    width: 18em;
    box-shadow: 0 5px 25px rgba(1 1 1/ 15%);
    border-radius: 10px;
    padding: 25px;
    margin: 15px;
    transition: 0.4s ease;
}


.card:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.info a {
    cursor: pointer;
    color: #4487fc;
    font-weight: 800;
}

.card img {
    max-width: 100%;
    max-height: 100%;
}
* {
    font-family: var(--font-family);
}

.services {
    padding: 5rem 10rem;
}

@media screen and (max-width: 600px) {
    .services {
        padding: 2rem 1rem;
    }
}

.stitle {
    font-size: 2em;
    text-align: center;
    color: var(--color-text);
    padding: 2em;
}

.scards{
    display: flex;
    background-color: rgba(0, 0, 0, 0.6);
    max-width: 90vw;
    border-radius: 10px;
}

.scard{
    flex: 0.5 1;
    margin: 1em;
    transition: 1s flex;
    color: white;
    height: 10em;
    box-shadow: 5px 5px 5px rgba(174, 0, 255, 0.288);

}
@media screen and (max-width: 1000px) {
    .scards{
        flex-direction: column;
    }
}

.scard p{
    opacity: 0;
    transition: 1s ease;
    margin: 1em;
}

.scard:hover{
    flex: 1.25 1;
}

.scard:hover p{
    opacity: 1;
}
.xxa__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
    position: fixed;
    z-index: 999;
    background-color: rgba(0, 0, 0, 0.411);
    min-width: 100vw;
}

.xxa__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

/* .xxa__navbar-links_logo {
    margin-right: 2rem;
}

.xxa__navbar-links_logo img {
    width: 62px;
    height: 16;
} */


.xxa__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.xxa__navbar-links_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.xxa__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.xxa__navbar-menu svg {
    cursor: pointer;
}

.xxa__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    text-transform: capitalize;
}

.xxa__navbar-menu_container p {
    margin: 1rem 0;
}

@media screen and (max-width: 1050px) {
    .xxa__navbar-links_container {
        display: none;
        
    }
    .xxa__navbar-menu {
        display: flex;
    }

    .xxa__navbar {
        background-color: rgba(255, 255, 255, 0);
    }
}

@media screen and (max-width: 700px) {
    .xxa__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .xxa__navbar {
        padding: 2rem;
    }

    .xxa__navbar-menu_container {
        top: 20px;
    }
}
