* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient__bg {

    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 10% 35%, rgba(123, 69, 210, 1) 0%, rgba(0, 5, 107, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 10% 35%, rgba(123, 69, 210, 1) 0%, rgba(0, 5, 107, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 10% 35%, rgba(123, 69, 210, 1) 0%, rgba(0, 5, 107, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 10% 35%, rgba(123, 69, 210, 1) 0%, rgba(0, 5, 107, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 10% 35%, rgba(123, 69, 210, 1) 0%, rgba(0, 5, 107, 1) 100%);

    position : relative;

      

}

.gradient__bg:after {
    content  : "";
    position : absolute;
    z-index  : 1;
    bottom   : 0;
    left     : 0;
    pointer-events   : none;
    background-image : linear-gradient(to bottom, 
                      rgba(255,255,255, 0), 
                      var(--color-bg) 90%);
    width    : 100%;
    height   : 4em;
  }

.gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clop: text;
    -webkit-text-fill-color: transparent;
}

.section__padding {
    padding: 4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
@keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }

@media screen and (max-width: 700px) {
    .section__padding {
        padding: 4rem;
    }

    .section__margin {
        margin: 4rem;
    }
}

@media screen and (max-width: 550px) {
    .section__padding {
        padding: 4rem 2rem;
    }

    .section__margin {
        margin: 4rem 2rem;
    }
}

